import { ADD_SUM, ADD_DATA } from '../constants/action-types';

const initialState = {
    data: [],
    sum: {}
};

function rootReducer(state = initialState, action) {
    if (action.type === ADD_DATA) {
        return Object.assign({}, state, {
            data: [
                {
                    avatarUrl: action.payload[0],
                    bio: action.payload[1],
                    email: action.payload[2],
                    location: action.payload[3],
                    login: action.payload[4],
                    name: action.payload[5],
                    url: action.payload[6]
                }
            ]
        });
    } else if (action.type === ADD_SUM) {
        return Object.assign({}, state, action.payload);
    }
    return state;
}

export default rootReducer;
